.home-left {
  display: flex;
  flex-direction: column;
  width: auto;
  background-color: #191a23;
  z-index: 40;
  box-shadow: 2px 0 6px rgba(0, 21, 41, 0.35);
}
.home-left .home-left-top {
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #101117;
  color: rgba(255, 255, 255, 0.9);
  font-weight: bolder;
  font-size: 15px;
  align-items: center;
}
.home-left .home-left-top .logo {
  height: 40px;
  width: auto;
  margin: 10px 10px 10px -5px;
}
.home-left .ivu-menu-vertical.ivu-menu-light:after {
  width: 0;
}
.home-left .ivu-menu::-webkit-scrollbar {
  width: 2px;
}
.home-left .ivu-menu::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  background: #ededed;
}
.home-left .ivu-menu::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: #f1f1f1;
}
