.home {
  width: 100%;
  height: 100%;
  /*background-color: #f5f7f9;*/
  background-color: #E8ECF0;
  overflow: hidden;
  display: flex;
}
.home .home-right {
  z-index: 10;
  flex-grow: 1;
  position: relative;
}
.home .home-right .home-right-header {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  z-index: 30;
  padding: 0 20px;
  align-items: center;
  height: 60px;
  background-color: white;
  box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
  display: flex;
  justify-content: space-between;
}
.home .home-right .home-right-header .path-guide {
  display: flex;
  align-items: center;
}
.home .home-right .home-right-header .path-guide .icon-menu {
  margin-right: 20px;
  cursor: pointer;
}
.home .home-right .home-right-header .path-guide .icon-menu:hover {
  background-color: #f5f7f9;
}
@media only screen and (min-width: 320px) and (max-width: 767px) {
  .home .home-right .home-right-header .path-guide .breadcrumb {
    display: none;
  }
}
.home .home-right .home-right-header .option-bar {
  display: flex;
  align-items: center;
}
.home .home-right .home-right-header .option-bar .login-user {
  display: flex;
  align-items: center;
  padding: 0 0 0 20px;
}
.home .home-right .home-content {
  box-sizing: border-box;
  position: absolute;
  z-index: 20;
  top: 0;
  width: 100%;
  height: 100%;
  padding: 70px 10px 10px 10px;
}
.home .home-right .home-content .paging {
  margin: auto;
  text-align: center;
}
